@charset "UTF-8";
html,
body,
h1,
h2,
div,
p,
input,
button {
  margin: 0;
  padding: 0;
  border: 0;
}

@font-face {
  font-family: "Share Tech";
  font-style: normal;
  font-weight: 400;
  src: local(""), url("../font/share-tech-v12-latin-regular.woff2") format("woff2"), url("../font/share-tech-v12-latin-regular.woff") format("woff");
}
body,
input,
button {
  font-family: "Share Tech", sans-serif;
  letter-spacing: 1px;
}

body {
  font-size: 12px;
}

input,
button {
  font-size: 14px;
}
input.ctrlProgress,
button.ctrlProgress {
  font-size: 26px;
}

button {
  font-weight: bold;
}

h1,
h2 {
  font-size: 14px;
  font-weight: bold;
}

label {
  font-weight: bold;
}

main .story {
  font-size: 18px;
  font-style: italic;
}

.playerName {
  font-weight: bold;
}

#app {
  display: none;
  min-width: 320px;
  max-width: 960px;
  margin: auto;
}

body {
  padding: 10px;
}

p {
  margin-bottom: 15px;
}

input,
button {
  padding: 3px;
}

input {
  width: 150px;
  border-bottom: 1px dashed;
}

button {
  cursor: pointer;
  border-radius: 5px;
}

main {
  display: block;
  margin-bottom: 50px;
}
main .sidebar {
  text-align: center;
  margin-bottom: 10px;
}
main .sidebar h1 {
  margin-bottom: 10px;
}
main .sidebar div {
  margin-bottom: 10px;
}
main .sidebar div.gameStats {
  text-align: center;
}
main .sidebar div.gameStats div {
  display: inline-block;
}
main .content {
  text-align: center;
}
main .content .story {
  margin-bottom: 60px;
}
main .content .actionLog {
  text-align: left;
}
main .content .actionLog p {
  margin-bottom: 5px;
  padding-bottom: 5px;
  border-bottom: 1px dotted;
}

.actionGif {
  display: inline-block;
  width: 160px;
  height: 120px;
  border: 1px solid;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.money:after {
  content: "â—ˆ";
}

.preloader {
  display: none;
}

@media (min-width: 500px) {
  main {
    display: grid;
    grid-template-columns: minmax(80px, 200px) auto;
  }
  main .sidebar {
    margin-right: 20px;
    text-align: center;
  }
  main .sidebar input,
main .sidebar button {
    display: block;
    margin-bottom: 5px;
    width: 100%;
  }
  main .sidebar div.gameStats div {
    display: block;
    margin-bottom: 5px;
  }
  main .content {
    margin-top: 22px;
    text-align: left;
  }

  .actionGif {
    width: 100%;
    height: 120px;
  }
}
body {
  color: #838E99;
  background-color: #212529;
}

h1,
h2 {
  color: #4FC6AB;
}

input {
  color: #838E99;
  background-color: transparent;
  border-color: #4FC6AB;
}
input:invalid {
  color: #ca483e;
}

button {
  color: #838E99;
  background-color: #30353A;
}
button:hover {
  color: #4FC6AB;
  background-color: #535B61;
}
button:disabled {
  color: #535B61;
  background-color: #30353A;
}

.actionGif {
  border-color: #535B61;
}

.money {
  color: #C99A1C;
}

.record .title {
  color: #6D7FBE;
}
.record .artist {
  color: #4F69C6;
}
.record .format {
  color: #896DD0;
}

.sys {
  color: #B6C1CB;
}

.warning {
  color: #ca483e;
}

.playerName {
  color: #4FC6AB;
}

